class Score {
  constructor(software) {
    this.software = software;
  }

  static calcCpuAndGpuScore(hardware) {
    switch (hardware.category) {
      case "low":
        return 3;
      case "middle":
        return 6;
      case "high":
        return 10;
      default:
        break;
    }
  }

  getCpuScore(cpu) {
    const score = this.constructor.calcCpuAndGpuScore(cpu);
    if (score >= this.software.cpu) {
      return this.getMessage("CPU", "success", score, cpu);
    } else if (score === this.software.cpu - 1) {
      return this.getMessage("CPU", "alert", score, cpu);
    } else {
      return this.getMessage("CPU", "danger", score, cpu);
    }
  }

  getGpuScore(gpu) {
    const score = this.constructor.calcCpuAndGpuScore(gpu);
    if (score >= this.software.gpu) {
      return this.getMessage("کارت گرافیک", "success", score, gpu);
    } else {
      return this.getMessage("کارت گرافیک", "danger", score, gpu);
    }
  }

  getRamScore(ram) {
    if (ram.score >= this.software.ram) {
      return this.getMessage("RAM", "success", ram.score, ram);
    } else {
      return this.getMessage("RAM", "danger", ram.score, ram);
    }
  }

  getHardScore(hard) {
    if (hard.score >= this.software.hard) {
      return this.getMessage("هارد", "success", hard.score, hard);
    } else {
      return this.getMessage("هارد", "danger", hard.score, hard);
    }
  }

  getMessage(hardwareType, status, score, hardware) {
    switch (status) {
      case "success":
        return {
          message: `${hardwareType} شما در وضعیت خوبی قرار دارد!`,
          status,
          score,
          name: hardware.name,
          hardwareType
        };

      case "danger":
        return {
          message: `${hardwareType} شما ضعیف است !`,
          status,
          score,
          name: hardware.name,
          hardwareType
        };

      case "warning":
        return {
          message: `${hardwareType} شما قابل قبول است!`,
          status,
          score,
          name: hardware.name,
          hardwareType
        };
    }
  }
}

export default Score;
